
export enum Status {
    LOADING = 'loading', 
    SUCCESS = 'success', 
    ERROR = 'error', 
  }
  
  export interface PizzaSliceState {
    items: Pizza[] ;
    status: Status;
  }
  
  export type Pizza = {
    id:string;
    name:string; 
    imageUrl:string; 
    price:number; 
    sizes:number[]; 
    types:number[]
  }

  export type SearchPizzaParams = {
    order:string;
    sortBy:string;
    category:string; 
    search:string; 
    currentPage:string; 
  }